.marquee {
  display: flex;
  width: 200%;
  animation: marquee-scroll 18s linear infinite;
}
.marquee-slow {
  animation-duration: 26s;
}
.marquee-fast {
  animation-duration: 16s;
}
.marquee-reverse {
  animation-direction: reverse;
