.tech-band {
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  overflow: hidden;
  background: radial-gradient(120% 120% at 10% 0%, rgba(32, 178, 170, 0.15), transparent 55%),
    radial-gradient(120% 120% at 90% 100%, rgba(255, 255, 255, 0.08), transparent 60%),
    rgba(255, 255, 255, 0.03);
}
.tech-mask {
  position: relative;
  overflow: hidden;
}
.tech-mask::before,
.tech-mask::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 60px;
  z-index: 2;
  pointer-events: none;
}
.tech-mask::before {
  left: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.95), transparent);
}
.tech-mask::after {
  right: 0;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0.95), transparent);
}
.tech-chip {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  padding: 10px 16px;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(0, 0, 0, 0.55);
  backdrop-filter: blur(6px);
  transition: transform 0.2s ease, border-color 0.2s ease, background 0.2s ease;
}
.tech-chip:hover {
  transform: translateY(-2px);
  border-color: rgba(32, 178, 170, 0.8);
  background: rgba(32, 178, 170, 0.08);
}
.tech-chip-alt {
  border-color: rgba(255, 255, 255, 0.16);
  background: rgba(255, 255, 255, 0.06);
}
.tech-logo {
  height: 20px;
  width: 20px;
  filter: drop-shadow(0 0 8px rgba(32, 178, 170, 0.25));
}
.scroll-reveal {
  opacity: 0;
  transform: translateY(24px);
  transition: opacity 0.7s ease, transform 0.7s ease;
  will-change: opacity, transform;
}
.scroll-reveal.is-visible {
  opacity: 1;
  transform: translateY(0);
}
@media (prefers-reduced-motion: reduce) {
  .scroll-reveal {
    opacity: 1;
    transform: none;
    transition: none;
  }
}
.footer-marquee {
  position: absolute;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
}
.footer-marquee-track {
  display: flex;
  width: 200%;
  animation: marquee-scroll 22s linear infinite;
}
.stroke-text {
  -webkit-text-stroke: 1px #ffffff;
  text-stroke: 1px #ffffff;
}
